.menu-mob{
  display: none;
}
a  {
  text-decoration: none;
}

.open{
  overflow: hidden;
  scroll-behavior: smooth;
}


header {
  width: 90%;
  height: 195px;
  text-align: left;
  padding: 20px 60px;
  color: #fff;
  border-bottom: 5px solid #ff7f1d;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  
  margin: 20px auto 10px auto;
}

header h2 {
  display: inline;
}

.cards-layout {
  width: 90%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin: 50px auto;
}

@media (max-width:1200px) {
  .cards-layout{
    width: 95%;
    justify-content:center;
  }
}

@media (max-width:768px) {
  .cards-layout{
    justify-content:space-evenly;
  }
  header{
    margin: 0px auto 30px auto;
  }
}


@media (max-width:768px) {
  
}

@media (max-width:425px) {
  header{
    width: 505px;
  }
 
  .cards-layout{
    width: 625px;
  }
  .menu-mob{
    position: absolute;
    display: block;
    right: 20px;
    top: 151px;
    
  }

  
}