.content-card {
    width: 20%;
    height: 350px;
    box-shadow: -1px -1px 30px #e2e2e2;
    display: flex;
    flex-direction: column;
    margin-bottom: 50px;
    justify-content: center;
    align-items: center;
    position: relative;
    border-radius: 8px;
    padding: 0 10px;
    text-align: center;
}

.content-card img {
    width: 40px;
    margin-bottom: -10px;
}

.content-card h2 {
    font-weight: normal;
}

.content-card img svg{
   background-color: red;
}

.content-card p:nth-child(3) {
    font-size: .9em;
    margin: -5px 0;
}

.info {
    width: 100%;
    height: 50px;
    background: -webkit-linear-gradient(top left,#ff7f1d, #f27f3d, #f25c05);
    bottom: 0;
    position: absolute;
    display: flex;
    justify-content: space-around;
    align-items: center;
    color: #fff;
    border-radius: 0 0 8px 8px;
}

.info .spaceF {
    text-decoration: none;
    font-weight: bold;
    color: #fff;
    margin-left: 60px;
}

@media (max-width:1200px) {
    .content-card {
       margin-right: 37px;
    }

    .info {
       font-size: 1em;
    }
  }

  @media (max-width:1024px) {
    .content-card {
      font-size: .9em;
    }
  }

  @media (max-width:768px) {
    .content-card {
      width: 40%;
      margin-right: 0;
    }
}
