section {
    width: 100%;
    height: 100%;
   
}

.title {
    margin-left: 40px;
   
}

.image{
    width: 150px;
    display: inline-block;
}

.image img {
    width: 95%;
    padding-right: 20px;
    border-right: 1px solid #fff;
   
}

.content-element {
    display: flex;
    align-items: center;
    width: 440px;
}

.text {
    width: 565px;
}

span {
  font-size: 2em;
  font-weight: 500;
}

.image-banner{
    position: absolute;
    width: 40%;
    height: 100%;
    background: no-repeat url('../assets/fluxo-de-caixa-de-investimentos.jpg');
    background-size: cover;
    top: 0;
    right: 0;
}

.content-img img{
    width: 100%;
}


