.back {
  text-decoration: none;
  color: orange;
  font-weight: bold;
  margin-left: 5%;
}

.card {
  width: 600px;
  height: 350px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  border-radius: 8px;
  box-shadow: -1px -1px 30px #ccc;
}

.content-img {
  width: 95%;
  height: 200px;
}

.content-text {
  text-align: left;
  font-size: 0.92em;
  color: orange;
  padding-left: 15px;
}

.btnDat {
  top: 91%;
  left: 23%;
}

.datText {
  width: 68%;
  margin: 15px auto;
  font-size: 1.1em;
}

.arrow {
  text-align: center;
  display: flex;
  align-items: center;
}

.arrow img {
  width: 60px;
}

@media (max-width: 1200px) {
  a {
    margin-left: 5%;
  }
  .card {
    margin-bottom: 29px;
  }

  .arrow {
    display: none;
  }
  .btnDat {
    top: 151.5%;
    left: 27%;
  }
}

@media (max-width: 1200px) {
  .btnDat {
    top: 151.5%;
    left: 29%;
  }
}

@media (max-width: 768px) {
  .back {
    margin-left: 10%;
  }

  .btnDat {
    top: 156.5%;
    left: 32%;
  }
}

@media (max-width: 425px) {
  .datText {
    font-size: 1em;
    margin-left: 44px;
    width: 540px;
  }
  .btnDat {
    top: 154.2%;
    left: 40%;
}
}


