

.menu{
    width: 100%;
    height: 80px;
   

}

.menu ul {
    width: 90%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #f2f2f2;
    margin: 0 auto;
    list-style: none;
    border-radius: 5px;
    padding: 0;
    border: 2px solid #e2e2e2;
}

.menu ul  li {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-right: 2px solid #e2e2e2;
   
}

.menu ul li img {
    width: 30px;
    margin-top: 5px;
}

.active {
    background-color: orange;
}
@media (max-width:1200px) {
    .menu ul  li {
       font-size: 85%;
       
    }
  }

  @media (max-width:425px) {
    .menu {
        width: 461px;
        height: 79%;
        position: fixed;
        z-index: 99;
        right: -450px;
        top: 239px;
        transition: .25s linear;
    }    
    .menu ul {
       flex-direction: column;
    }

    .menu ul li{
    font-size: 120%;
    border-bottom: 2px solid #e2e2e2;
     }

     .open{
        right: 0;
        
    }
     
  }

